  .bloc {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    z-index: 29;
    position: fixed;
    .bloc_block {
      width: 150px;
      height: 100%;
      position: fixed;

      padding-top: 140px;
      background: linear-gradient(
        135deg,
        #e4e4e469 0%,
        #272424bb 50%,
        rgba(185, 193, 201, 0.075) 90%
      );
      border-top-left-radius: 200px;
      border-bottom-left-radius: 200px;
      z-index: 30;
      box-shadow: -3px 5px 30px rgba(0, 0, 0, 0.904);
      .combo {
        border-radius: 50%;
        height: 100px;
        width: 100px;
        margin: 30px;
        color: rgba(245, 8, 39, 0.678);
        background-color: rgba(241, 241, 241, 0.753);
        box-shadow: -2px 2px 15px rgba(252, 6, 6, 0.938);
        z-index: 40;
        font-family: "Lobster", cursive;
        font-family: "Philosopher", sans-serif;
        font-size: 15px;
        span {
          svg {
            width: 70px;
            height: 70px;
          }
          .mix {
            animation: phone 0.08s ease-in-out infinite;
          }
          @keyframes phone {
            0%,
            100% {
              transform: rotate(-10deg);
            }
            50% {
              transform: rotate(10deg);
            }
          }
        }
      }
      .combo:hover {
        background-color: rgba(252, 6, 6, 0.37);
        box-shadow: -2px 2px 40px rgba(252, 6, 6, 0.938);
      }

      .combo1 {
        border-radius: 50%;
        height: 100px;
        width: 100px;
        margin: 30px;
        color: rgb(204, 170, 19);
        background-color: rgba(241, 241, 241, 0.753);
        box-shadow: -2px 2px 15px rgba(252, 207, 6, 0.938);
        z-index: 41;
        font-family: "Lobster", cursive;
        font-family: "Philosopher", sans-serif;
        font-size: 15px;
        svg {
          width: 80px;
          height: 80px;
        }
      }

      .combo1:hover {
        background-color: rgba(204, 170, 19, 0.473);
        box-shadow: -2px 2px 40px rgba(252, 207, 6, 0.938);
      }
      .combo2 {
        border-radius: 50%;
        height: 100px;
        width: 100px;
        margin: 30px;
        color: rgba(12, 180, 63, 0.993);
        background-color: rgba(241, 241, 241, 0.753);
        box-shadow: -2px 2px 15px rgba(6, 252, 80, 0.938);
        z-index: 42;
        font-family: "Lobster", cursive;
        font-family: "Philosopher", sans-serif;
        font-size: 15px;
        svg {
          padding-right: 5px;
          width: 90px;
          height: 90px;
        }
      }
      .combo2:hover {
        background-color: rgba(12, 180, 62, 0.315);
        box-shadow: -2px 2px 40px rgba(6, 252, 80, 0.938);
      }
      .combo span {
        opacity: 1;
        animation-name: glowing;
        animation-duration: 3s;
        animation-iteration-count: infinite;
      }
      .combo1 span {
        opacity: 1;
        animation-name: glowing1;
        animation-duration: 4s;
        animation-iteration-count: infinite;
      }
      .combo2 span {
        opacity: 1;
        animation-name: glowing2;
        animation-duration: 5s;
        animation-iteration-count: infinite;
      }

      @keyframes glowing {
        0%,
        100% {
          opacity: 1;
          filter: blur(20px);
          text-shadow: 0px 0px 10px rgb(245, 8, 40), 0 0 20px rgb(245, 8, 40),
            0 0 20px rgb(245, 8, 40), 0 0 20px rgb(245, 8, 40),
            0 0 30px rgb(245, 8, 40), 0 0 30px rgb(245, 8, 40),
            0 0 50px rgb(245, 8, 40), 0 50px 1280px rgb(245, 8, 40),
            0 10px 100px rgb(245, 8, 40), 0 10px 100px rgb(245, 8, 40),
            0 -10px 100px rgb(245, 8, 40), 0 -10px 100px rgb(245, 8, 40),
            0 -10px 100px rgb(245, 8, 40), 0 -10px 100px rgb(245, 8, 40);
        }
        7%,
        93% {
          opacity: 1;
          filter: blur(0);
          text-shadow: none;
        }
      }
      @keyframes glowing1 {
        0%,
        100% {
          opacity: 1;
          filter: blur(5px);
          text-shadow: 0px 0 10px rgb(252, 207, 6), 0 0 20px rgb(252, 207, 6),
            0 0 40px rgb(252, 207, 6), 0 0 80px rgb(252, 207, 6),
            0 0 160px rgb(252, 207, 6), 0 0 320px rgb(252, 207, 6),
            0 0 640px rgb(252, 207, 6);
        }
        7%,
        93% {
          opacity: 1;
          filter: blur(0);
          text-shadow: none;
        }
      }
      @keyframes glowing2 {
        0%,
        100% {
          opacity: 1;
          filter: blur(5px);
          text-shadow: 0px 0px 10px rgba(6, 252, 80, 0.993),
            0 0 20px rgba(6, 252, 80, 0.993), 0 0 40px rgba(6, 252, 80, 0.993),
            0 0 80px rgba(6, 252, 80, 0.993), 0 0 160px rgba(6, 252, 80, 0.993),
            0 0 320px rgba(6, 252, 80, 0.993), 0 0 640px rgba(6, 252, 80, 0.993);
        }
        7%,
        93% {
          opacity: 1;
          filter: blur(0);
          text-shadow: none;
        }
      }
      .glowing,
      .glowing1,
      .glowing2 {
        animation-duration: 8s;
      }
    }
  }
